import React, {useState} from 'react';
import Carousel, {Dots} from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';

import Box from '@mui/material/Box';

import Grid from '@mui/material/Grid';

import PropTypes from 'prop-types';

import {styled} from '@mui/material/styles';
const BrainHubSliderRoot = styled(Box)(({theme}) => {
  return {
    position: 'relative',
    display: 'flex',
    '& .BrainhubCarousel__container': {
      marginLeft: 20,
      marginRight: 20,
      maxWidth: 300,
      [theme.breakpoints.up('sm')]: {
        maxWidth: 450,
      },
      '& .BrainhubCarousel': {
        height: '100%',
      },
    },
    '& .BrainhubCarousel__dots': {
      [theme.breakpoints.down('sm')]: {
        display: 'none',
      },
      '& .BrainhubCarousel__thumbnail': {
        opacity: 1,
        backgroundColor: 'transparent',
        borderRadius: 10,
        marginBottom: 10,
        border: '1px solid #A0A5B9',
        '&.BrainhubCarousel__thumbnail--selected': {
          border: `solid 2px #7c7c7c`,
        },
      },
      flexDirection: 'column',
      '& img': {
        height: 80,
      },
    },
  };
});

const ProductImageSlide = ({product}) => {
  const [value, setValue] = useState(0);

  const slides = product.sm_pictures
  .map((data) => (
    <img src={data.url} alt='' key={data.url} />
  ));

  const onChange = (value) => {
    setValue(value);
  };

  return (
    <Grid item sm={12} md={4}>
      <BrainHubSliderRoot>
        <Dots
          position='left'
          thumbnails={slides}
          value={value}
          onChange={onChange}
          number={slides.length}
        />
        <Carousel
          position='left'
          value={value}
          slides={slides}
          onChange={onChange}
        />
      </BrainHubSliderRoot>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          mt: 5,
        }}
      ></Box>
    </Grid>
  );
};

export default ProductImageSlide;

ProductImageSlide.propTypes = {
  product: PropTypes.object,
};
