import React, {useEffect, useState} from 'react';
import {Box} from '@mui/material';
import AppCircularProgress from '../../../../../@crema/core/AppCircularProgress';
import {Fonts} from '../../../../../shared/constants/AppEnums';
import StarRateIcon from '@mui/icons-material/StarRate';
import AppLinearProgress from '../../../../../@crema/core/AppLinearProgress';
import PropTypes from 'prop-types';
const ReviewInfo = ({reviews}) => {
  const [reviewsInHash, setreviewsInHash] = useState([0, 0, 0, 0, 0]);
  const [avarage, setavarage] = useState(0);

  const getPercentage = (value) => {
    return (value / 5) * 100;
  };

  const getBarColor = (value) => {
    if (Number(value) <= 1) {
      return '#FF6161';
    }
    if (Number(value) > 1 && Number(value) <= 3) {
      return '#FF9F00';
    }
    if (Number(value) > 3) {
      return '#49BD65';
    }
  };
  const getBarPercentage = (value) => {
    return (value / reviews.length) * 100;
  };

  useEffect(() => {
    setreviewsInHash((prev) => {
      var sumOfRev = 0;
      reviews.map((review) => {
        if (review.rating) {
          prev[review.rating - 1]++;
          sumOfRev += review.rating;
        }
      });
      setavarage(sumOfRev / reviews.length);
      return prev;
    });
  }, [reviews]);

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: {xs: 'column', md: 'row'},
        alignItems: {xs: 'center', md: 'flex-start'},
      }}
    >
      <Box
        component='span'
        sx={{
          mr: {xs: 0, md: 5},
        }}
      >
        <AppCircularProgress
          minWidth={150}
          maxWidth={150}
          activeColor={getBarColor(avarage)}
          value={getPercentage(avarage)}
          hidePercentage
          centerNode={
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              }}
            >
              <Box
                component='h3'
                sx={{
                  fontSize: 14,
                  display: 'flex',
                  alignItems: 'center',
                  fontWeight: Fonts.BOLD,
                }}
              >
                {avarage.toFixed(1)} <StarRateIcon />
              </Box>
              <Box
                component='p'
                sx={{
                  ml: 2,
                  fontSize: 14,
                  fontWeight: Fonts.LIGHT,
                }}
              >
                Overall rating
              </Box>
            </Box>
          }
          thickness={2}
        />
      </Box>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        <Box
          sx={{
            p: 1,
            fontSize: 14,
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <Box component='span'>5</Box>
          <StarRateIcon style={{fontSize: 16}} />
          <AppLinearProgress
            activeColor='#49BD65'
            value={getBarPercentage(reviewsInHash[4])}
            thickness={6}
            style={{minWidth: 200, maxWidth: 500}}
          />
          <Box
            component='span'
            sx={{
              ml: 2,
              fontSize: 14,
              color: 'text.secondary',
            }}
          >
            {reviewsInHash[4]}
          </Box>
        </Box>
        <Box
          sx={{
            p: 1,
            fontSize: 14,
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <Box component='span'>4</Box>
          <StarRateIcon style={{fontSize: 16}} />
          <AppLinearProgress
            activeColor='#49BD65'
            value={getBarPercentage(reviewsInHash[3])}
            thickness={6}
            style={{minWidth: 200, maxWidth: 500}}
          />
          <Box
            component='span'
            sx={{
              ml: 2,
              fontSize: 14,
              color: 'text.secondary',
            }}
          >
            {reviewsInHash[3]}
          </Box>
        </Box>
        <Box
          sx={{
            p: 1,
            fontSize: 14,
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <Box component='span'>3</Box>
          <StarRateIcon style={{fontSize: 16}} />
          <AppLinearProgress
            activeColor='#FF9F00'
            value={getBarPercentage(reviewsInHash[2])}
            thickness={6}
            style={{minWidth: 200, maxWidth: 500}}
          />
          <Box
            component='span'
            sx={{
              ml: 2,
              fontSize: 14,
              color: 'text.secondary',
            }}
          >
            {reviewsInHash[2]}
          </Box>
        </Box>
        <Box
          sx={{
            p: 1,
            fontSize: 14,
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <Box component='span'>2</Box>
          <StarRateIcon style={{fontSize: 16}} />
          <AppLinearProgress
            activeColor='#FF9F00'
            value={getBarPercentage(reviewsInHash[1])}
            thickness={6}
            style={{minWidth: 200, maxWidth: 500}}
          />
          <Box
            component='span'
            sx={{
              ml: 2,
              fontSize: 14,
              color: 'text.secondary',
            }}
          >
            {reviewsInHash[1]}
          </Box>
        </Box>
        <Box
          sx={{
            p: 1,
            fontSize: 14,
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <Box component='span'>1</Box>
          <StarRateIcon style={{fontSize: 16}} />
          <AppLinearProgress
            activeColor='#FF6161'
            value={getBarPercentage(reviewsInHash[0])}
            thickness={6}
            style={{minWidth: 200, maxWidth: 500}}
          />
          <Box
            component='span'
            sx={{
              ml: 2,
              fontSize: 14,
              color: 'text.secondary',
            }}
          >
            {reviewsInHash[0]}
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default ReviewInfo;

ReviewInfo.propTypes = {
  reviews: PropTypes.array,
};
