import React, {useEffect, useState} from 'react';
import ProductImageSlide from './ProductImageSlide';
import AppCard from '../../../@crema/core/AppCard';
import Header from './Header';
import ProductView from './ProductView/index';
import AppGridContainer from '../../../@crema/core/AppGridContainer';

import {AppInfoView} from '../../../@crema';
import AppAnimate from '../../../@crema/core/AppAnimate';
import PropTypes from 'prop-types';
import {useLocation} from 'react-router-dom';
import {getProductDetail} from '../../../redux/actions';
import {useDispatch, useSelector} from 'react-redux';

const ProductDetail = () => {
  // const {state} = useLocation();
  const [products, setProducts] = useState(useLocation().state);
  const prod = useSelector(({ecommerce}) => ecommerce.currentProduct);
  const dispatch = useDispatch();
  const [loading, setloding] = useState(true);
  const id = useLocation().pathname.split('/')[3];
  console.log(id);
  useEffect(() => {
    dispatch(getProductDetail(id));
    // setProducts(list);
  }, [dispatch]);
  useEffect(() => {
    if (prod) {
      setProducts(prod);
      setloding(false);
    }
  }, [prod]);

  return (
    <>
      {!loading ? (
        <AppAnimate animation='transition.slideUpIn' delay={200}>
          <AppCard>
            <Header product={products} />
            <AppGridContainer>
              <ProductImageSlide product={products} />
              <ProductView product={products} />
            </AppGridContainer>
          </AppCard>
        </AppAnimate>
      ) : null}
      <AppInfoView />
    </>
  );
};

export default ProductDetail;

ProductDetail.propTypes = {
  match: PropTypes.object,
};
