import {firebase} from '@crema/services/auth/firebase/firebase';
import jwtAxios from '@crema/services/auth/jwt-auth';

import {useFormik} from 'formik';
import PropTypes from 'prop-types';
import {useEffect, useState} from 'react';
import * as Yup from 'yup';
import DamagedProductModalUI from '../components/DamagedProductModalUI';

const AddDamagedProductModal = ({open, onClose, productId}) => {
  const [showColor, setShowColor] = useState(false);
  const [showSize, setShowSize] = useState(false);
  const [showOkCanc, setShowOkCanc] = useState(false);
  const [addDamaged, setAddDamaged] = useState(false);
  const [loadingGetProduct, setLoadingGetProduct] = useState(false);
  const [product, setProduct] = useState();

  const formik = useFormik({
    initialValues: {
      productId: productId ? productId : '',
      color: '',
      size: '',
      count: '',
      isFromOrder: false,
    },
    validationSchema: Yup.object().shape({
      productId: Yup.string().required('Not a product name'),
      color: Yup.string(),
      count: Yup.number().required('Not a count'),
      size: Yup.string(),
      isFromOrder: Yup.bool(),
    }),
    onSubmit: async (values) => {
      try {
        setAddDamaged(true);

        const damaged_product = {
          auther: firebase.auth().currentUser.displayName,
          color: values.color ? values.color : '',
          count: values.count,
          productId: values.productId,
          size: values.size ? values.size : '',
          time: Date.now(),
          thisReportFromOrder: values.isFromOrder,
        };

        const prod = firebase
          .firestore()
          .collection('products')
          .doc(product.id + '');

        let countSum = await firebase
          .firestore()
          .collection('databaseCount')
          .doc('damagedProduct')
          .get();
        let count = countSum.data().count + 1;
        let sum = countSum.data().sum + formik.values.count;
        await firebase
          .firestore()
          .collection('databaseCount')
          .doc('damagedProduct')
          .update({count: count, sum: sum});

        await firebase
          .firestore()
          .runTransaction(async (transaction) => {
            return await transaction.get(prod).then(async (sfDoc) => {
              if (!sfDoc.exists) {
                throw 'Document does not exist!';
              }
              console.log(sfDoc.data());
              let val = validate(sfDoc.data());
              if (!val) {
                throw new Error('validationError');
              }
              let nprod = setNewProduct(sfDoc.data());
              await firebase
                .firestore()
                .collection('damaged_product')
                .add(damaged_product);

              transaction.update(prod, {...nprod});
            });
          })
          .then(() => {
            console.log('Transaction successfully committed!');
            onClose();
            formik.setValues({
              productId: '',
              color: '',
              size: '',
              count: '',
              isFromOrder: false,
            });
            return true;
          })
          .catch((error) => {
            console.log('Transaction failed: ', error);
            return false;
          });
        setAddDamaged(false);
      } catch (error) {
        setAddDamaged(false);
        console.log(error);
      }
    },
  });
  const setNewProduct = (product) => {
    let values = formik.values;
    let nproduct = product;
    if (!values.isFromOrder) {
      if (showColor && !showSize) {
        nproduct.variants.map((item, index) => {
          if (item.color_name == values.color) {
            nproduct.variants[index].count =
              Number(item.count) - Number(values.count);
          }
        });
      }
      if (!showColor && showSize) {
        nproduct.variants[0].size.map((item, index) => {
          if (item.name == values.size) {
            nproduct.variants[0].size[index].count =
              Number(item.count) - Number(values.count);
          }
        });
      }
      if (showColor && showSize) {
        nproduct.variants.map((itemColor, index) => {
          if (itemColor.color_name == values.color) {
            itemColor.size.map((itemSize, sIndex) => {
              if (itemSize.name == values.size) {
                nproduct.variants[index].size[sIndex].count =
                  Number(itemSize.count) - Number(values.count);
              }
            });
          }
        });
      }

      nproduct.stock = nproduct.stock - Number(values.count);
    } else {
      nproduct.sold = nproduct.sold - Number(values.count);
    }
    nproduct.damegdCouont = nproduct.damegdCouont + Number(values.count);

    return nproduct;
  };
  const validate = (product) => {
    console.log(product);
    let values = formik.values;
    if (0 > Number(values.count)) {
      formik.setFieldError('count', 'can not be minas ');
    }
    if (product.stock < values.count) {
      formik.setFieldError(
        'count',
        'the number in the stock is ' + product.stock,
      );
      return false;
    }

    if (
      (product.sold <= 0 || values.count > product.sold) &&
      values.isFromOrder
    ) {
      formik.setFieldError(
        'count',
        'the sold products are less than' + values.count,
      );
      return false;
    }
    if (showColor) {
      if (!values.color) {
        formik.setFieldError('color', 'this field is requerid');
        return false;
      }
      let variant = product.variants.filter(
        (item) => values.color === item.color_name,
      )[0];
      if (!variant) {
        formik.setFieldError('color', 'no such color name');
        return false;
      }
      if (variant.count) {
        if (values.count > variant.count) {
          formik.setFieldError(
            'count',
            'the number in the stock is ' + variant.count,
          );
          return false;
        }
      }
    }
    if (showSize) {
      if (!values.size) {
        formik.setFieldError('size', 'this field is requerid');
        return false;
      }
      let variant = product.variants.filter(
        (item) =>
          values.color === item.color_name || item.color_name === 'default',
      )[0];

      if (!variant) {
        formik.setFieldError('color', 'no such color name');
        return false;
      }
      let sizeVariant = variant.size.filter(
        (item) => values.size === item.name,
      )[0];
      if (!sizeVariant) {
        formik.setFieldError('size', 'the size not in the stock');
        return false;
      }
      if (sizeVariant) {
        if (values.count > sizeVariant.count) {
          formik.setFieldError(
            'count',
            'the number in the stock is ' + sizeVariant.count,
          );
          return false;
        }
      }
    }
    return true;
  };

  const getProductForAddReport = () => {
    setLoadingGetProduct(true);
    jwtAxios
      .get('/api/ecommerce/get', {
        params: {id: formik.values.productId},
      })
      .then((data) => {
        setProduct(data.data);
        if (
          data.data.variants?.length > 0 &&
          data.data.variants[0].color_name !== 'default'
        ) {
          setShowColor(true);
        } else {
          setShowColor(false);
        }
        if (data.data.variants[0]?.size?.length > 0) {
          setShowSize(true);
        } else {
          setShowSize(false);
        }
      })
      .finally(() => {
        setLoadingGetProduct(false);
        setShowOkCanc(true);
      });
  };

  useEffect(() => {
    setShowOkCanc(false);
    setShowColor(false);
    setShowSize(false);
    formik.setFieldValue('color', '');
    formik.setFieldValue('size', '');
  }, [formik.values.productId]);
  useEffect(() => {
    return () => {
      formik.resetForm();
      formik.handleReset();
    };
  }, []);
  return (
    <DamagedProductModalUI
      open={open}
      loadingGetProduct={loadingGetProduct}
      onClose={onClose}
      showColor={showColor}
      addDamaged={addDamaged}
      formik={formik}
      showOkCanc={showOkCanc}
      showSize={showSize}
      getProductForAddReport={getProductForAddReport}
    />
  );
};
export default AddDamagedProductModal;

AddDamagedProductModal.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  productId: PropTypes.string,
};
