import {LoadingButton} from '@mui/lab';
import {
  Button,
  Card,
  CardContent,
  Checkbox,
  Collapse,
  Fade,
  FormControlLabel,
  Grid,
  Modal,
  TextField,
} from '@mui/material';
import PropTypes from 'prop-types';

const DamagedProductModalUI = ({
  isEdit,
  getProductForAddReport,
  formik,
  showSize,
  showOkCanc,
  addDamaged,
  showColor,
  loadingGetProduct,
  onClose,
  open,
}) => {
  return (
    <Modal open={open} onClose={() => onClose()}>
      <Fade in={open}>
        <form onSubmit={formik.handleSubmit}>
          <Card
            sx={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              width: '60%',
              p: 4,
            }}
          >
            <CardContent>
              <Grid container spacing={4} m={2}>
                Report Damaged product
              </Grid>
              <Grid container item xs={12} spacing={4} textAlign={'start'}>
                <Grid item xs={12} lg={6} textAlign={'start'}>
                  <TextField
                    name='productId'
                    type='text'
                    required
                    label={'Product Id'}
                    variant='outlined'
                    disabled={formik.values.productId || isEdit ? true : false}
                    value={formik.values.productId}
                    onChange={formik.handleChange}
                    error={formik.errors?.productId}
                    helperText={
                      formik.errors?.productId
                        ? formik.errors.productId.toString()
                        : ''
                    }
                  />
                </Grid>
                <Grid item xs={12} lg={6} textAlign={'start'}>
                  <TextField
                    required
                    type='number'
                    label={'Count'}
                    variant='outlined'
                    name='count'
                    value={formik.values.count}
                    onChange={formik.handleChange}
                    error={formik.errors?.count}
                    helperText={formik.errors?.count ? formik.errors.count : ''}
                  />
                </Grid>
                <Grid item xs={12} textAlign={'start'}>
                  {!loadingGetProduct ? (
                    <Button
                      variant='contained'
                      onClick={() => {
                        getProductForAddReport();
                      }}
                    >
                      Create Report
                    </Button>
                  ) : (
                    <LoadingButton loading variant='outlined'>
                      hi
                    </LoadingButton>
                  )}
                </Grid>
                <Grid item xs={12} lg={6} textAlign={'start'}>
                  <Collapse in={showColor}>
                    <TextField
                      fullwidth
                      required={showColor}
                      label={'Color'}
                      variant='outlined'
                      disabled={isEdit ? true : false}
                      name='color'
                      value={formik.values.color}
                      onChange={formik.handleChange}
                      error={
                        formik.errors?.color ? formik.errors?.color : false
                      }
                      helperText={
                        formik.errors?.color ? formik.errors.color : ''
                      }
                    />
                  </Collapse>
                </Grid>

                <Grid item xs={12} lg={6}>
                  <Collapse in={showSize}>
                    <TextField
                      fullwidth
                      label={'Size'}
                      disabled={isEdit ? true : false}
                      variant='outlined'
                      required={showSize}
                      name='size'
                      value={formik.values?.size}
                      onChange={formik.handleChange}
                      error={formik.errors?.size ? formik.errors?.size : false}
                      helperText={formik.errors?.size ? formik.errors.size : ''}
                    />
                  </Collapse>
                </Grid>

                <Grid item xs={12} textAlign={'start'}>
                  <FormControlLabel
                    name='isFromOrder'
                    control={
                      <Checkbox
                        disabled={isEdit ? true : false}
                        checked={formik.values?.isFromOrder}
                        onChange={formik.handleChange}
                      />
                    }
                    label='This report from order'
                  />
                </Grid>
                <Collapse in={showOkCanc}>
                  <Grid item container xs={12} spacing={5}>
                    <Grid item xs={12} lg={6}>
                      <Button
                        variant='contained'
                        color='warning'
                        onClick={() => {
                          onClose();
                        }}
                      >
                        Canceled
                      </Button>
                    </Grid>
                    <Grid item x={12} lg={6}>
                      {!addDamaged ? (
                        <Button type='submit' variant='contained'>
                          {isEdit ? 'Edit' : 'Okay'}
                        </Button>
                      ) : (
                        <LoadingButton loading variant='outlined'>
                          hi
                        </LoadingButton>
                      )}
                    </Grid>
                  </Grid>
                </Collapse>
              </Grid>
            </CardContent>
          </Card>
        </form>
      </Fade>
    </Modal>
  );
};

export default DamagedProductModalUI;
DamagedProductModalUI.propTypes = {
  open: PropTypes.bool.isRequired,
  formik: PropTypes.object.isRequired,
  addDamaged: PropTypes.bool.isRequired,
  getProductForAddReport: PropTypes.func.isRequired,
  showOkCanc: PropTypes.bool.isRequired,
  loadingGetProduct: PropTypes.bool.isRequired,
  showSize: PropTypes.bool.isRequired,
  showColor: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  isEdit: PropTypes.bool,
};
