import React from 'react';
import Box from '@mui/material/Box';
import Rating from '@mui/material/Rating';
import {Fonts} from '../../../shared/constants/AppEnums';
import PropTypes from 'prop-types';
import {Button} from '@mui/material';
import {useEffect, useState} from 'react';
import {useIntl} from 'react-intl';
import {useNavigate} from 'react-router-dom';
import AddDamagedProductModal from '../ReportDamagedProduct/AddDamagedProductModal/AddDamagedProductModal';

const Header = ({product}) => {
  const {messages} = useIntl();

  const navigation = useNavigate();
  const [avarage, setavarage] = useState(0);
  const [openDamagedProduct, setOpenDamagedProduct] = useState(false);

  useEffect(() => {
    var sumOfRev = 0;
    product.reviews?.map((review) => {
      if (review.rating) {
        sumOfRev += review.rating;
      }
    });

    setavarage(sumOfRev / product.review);
  }, [product]);

  const handleViewOrder = () => {
    navigation('/product-management/EditProductForm/' + product.id, {
      state: product,
    });
  };

  return (
    <Box
      sx={{
        display: 'flex',
        alignItems: 'center',
        flexDirection: {xs: 'column', sm: 'row'},
        pb: 5,
        mb: 5,
        borderBottom: '1px solid #E5E4EA',
      }}
    >
      <Box
        sx={{
          flex: 1,
        }}
      >
        <Box
          component='h3'
          sx={{
            color: 'text.primary',
            fontWeight: Fonts.BOLD,
            fontSize: 16,
            mb: 1,
          }}
        >
          {product.name}
        </Box>
        <Box
          sx={{
            display: 'flex',
          }}
        >
          <Rating value={Number(avarage).toFixed(1)} size='small' readOnly />
          <Box
            sx={{
              ml: 2,
              mr: 4,
              color: 'text.secondary',
            }}
          >
            {product.review + ' reviews'}
          </Box>

          <Box>
            <Box
              component='span'
              sx={{
                ml: 2,
                color: 'text.secondary',
              }}
            ></Box>
          </Box>
        </Box>
      </Box>

      <Box
        sx={{
          display: 'flex',
          mt: {xs: 4, sm: 0},
        }}
      >
        <Box
          display='flex'
          justifyContent={'flex-end'}
          flexDirection='row'
          alignItems='left'
        >
          <Button variant='contained' color='primary' onClick={handleViewOrder}>
            {messages['ecommerce.editproduct']}
          </Button>
        </Box>
        <Box
          display='flex'
          justifyContent={'flex-end'}
          flexDirection='row'
          alignItems='left'
          mx={1}
        >
          <Button
            variant='contained'
            color='primary'
            onClick={() => setOpenDamagedProduct(true)}
          >
            {messages['ecommerce.addDamaged']}
          </Button>
        </Box>
      </Box>
      <AddDamagedProductModal
        onClose={() => setOpenDamagedProduct(false)}
        open={openDamagedProduct}
        productId={product.id}
      />
    </Box>
  );
};

export default Header;

Header.propTypes = {
  product: PropTypes.object,
};
